//引入仓库，状态机
import {
	createNamespacedHelpers
} from 'vuex';
const {
	mapActions,
	mapState,
	mapMutations
} = createNamespacedHelpers('pet');
export default {
	data() {
		return {
			shop_id: '',
			pet_id: '',
			target: false,
			count: 0
		};
	},
	//页面加载的时候就修改id值
	onLoad: function(option) {
		(this.shop_id = option.shop_id), (this.pet_id = option.pet_id);
	},
	mounted() {
		this.getuser();
		// console.log(this.shop_id, this.pet_id);
		//用一个宠物的id去拿明细渲染页面
		this.getpetBypetId(this.pet_id);
	},
	methods: {
		...mapActions(['getpetBypetId', 'getuser', 'addShoppingAart']),
		gwc() {
			this.target = !this.target;
			console.log(11111);
		},
		//减少事件
		reduce() {
			this.count = this.count - 1;
		},
		//增加事件
		add() {
			this.count = ~~this.count + 1;
		},
		//确认加入购物车事件
		async optInBtn() {
			this.target = false;
			if (this.shop_id == '') {
				this.shop_id = this.onepetData.shopsID[0];
			}
			const result = await this.addShoppingAart({
				//宠物id
				petId: this.pet_id,
				//商店id
				shopId: this.shop_id,
				//购买数量
				count: this.count,
				//宠物名字
				name: this.onepetData.name,
				//宠物价格
				price: this.onepetData.price,
				//宠物库存
				stock: this.onepetData.inventory,
				//宠物图片
				img: this.onepetData.imageName[0]
			});
			// console.log(result)
			if (result[1].data._id) {
				uni.showModal({
					title: '提示',
					content: '商品加入购物车成功'
				});
			}
		},
		//立即支付事件
		buyImmediately() {
			if (this.shop_id == '') {
				this.shop_id = this.onepetData.shopsID[0];
			}
			let arry = [{
				//宠物id
				petId: this.pet_id,
				//商店id
				shopId: this.shop_id,
				//购买数量
				count: 1,
				//宠物名字
				name: this.onepetData.name,
				//宠物价格
				price: this.onepetData.price,
				//宠物库存
				stock: this.onepetData.inventory,
				//宠物图片
				img: this.onepetData.imageName[0],
				chenck: true,
				//用户id
				userId: this.loginId,
			}];

			//点击立即购买吧立即购买的商品本地储存再跳转到订单支付页面
			try {
				uni.setStorageSync('arry', arry);
			} catch (e) {
				// error
			};

			uni.navigateTo({
				url: '/pages/PaymentOfAnOrder/PaymentOfAnOrder'
			});

		}
	},
	computed: {
		...mapState(['onepetData', 'loginId'])
	}
};
